.login-container
{
    display: flex;
    flex-direction: column;
    background-color:#00A7F8;
    height: 100vh;
    align-items: center;
 

}
.create-account
{
    color: white;
    font-family:roboto;
    font-size:50px;
}
.form-container
{
    display: flex;
    flex-direction: column;
    height: 60vh;
    width: 80%;
    align-items: center;
    margin-top: 10px;
}

@media screen and (min-width:576px){
.form-container
{
width: 40%;
}   
}
.ip-style
{
    width: 97%;
    margin-top: 5px;
    margin: 5px;
    border-radius: 4px;
    height: 45%;
    border: 1px solid #3B4856;
    padding: 5px;
    color:#3B4856;
}

.img-login
{
    width: 370px;
    height: 390px;
    margin-bottom: 10px ;
}

.submit-form
{
    background-color: #3B4856;
    color:white;
    padding: 10px;
    height: 34  px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border: 0px;
    font-size: 15px;
    width: 140px;
    margin-top: 10px;
}

.err-msg
{
    color:red;
    align-self: flex-start;
    margin-left: 2px;
    font-weight: 400;
}